.image-fader {
  position: relative;
}

.image-fader img {
  height: 100px;
}

.image-fader img.overlay {
  position: absolute;
  left: 0;
  opacity: 0;
  -webkit-transition: opacity 0.25s ease-in-out;
  -o-transition: opacity 0.25s ease-in-out;
  transition: opacity 0.25s ease-in-out;
}

.image-fader img.overlay.visible {
  opacity: 1;
}



